"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Skeleton } from "@/components/ui/skeleton"
import { useContract } from "@/context/contract-context"
import { formatDate } from "@/lib/utils"

export function ActiveCampaigns() {
  const { contract, isConnected } = useContract()
  const [campaigns, setCampaigns] = useState([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    if (contract) {
      fetchCampaigns()
    }
  }, [contract])

  const fetchCampaigns = async () => {
    try {
      setLoading(true)

      // Get campaign count from contract
      const count = await contract.campaignCount()
      const campaignsList = []

      // Fetch each campaign's data
      for (let i = 1; i <= count; i++) {
        try {
          // Get basic campaign info
          const [votingType, restriction, resultType, creator, description] = await contract.getCampaignBasicInfo(i)
          
          // Get campaign time info
          const [status, startTime, endTime] = await contract.getCampaignTimeInfo(i)
          
          // Format campaign data
          const campaignData = {
            id: i,
            description: description,
            creator: creator,
            startTime: Number(startTime),
            endTime: Number(endTime),
            status: Number(status),
            restriction: Number(restriction),
            resultType: Number(resultType),
            votingType: Number(votingType)
          }

          campaignsList.push(campaignData)
        } catch (err) {
          console.error(`Error fetching campaign ${i}:`, err)
        }
      }

      setCampaigns(campaignsList)
    } catch (error) {
      console.error("Error fetching campaigns:", error)
    } finally {
      setLoading(false)
    }
  }

  const getStatusBadge = (startTime, endTime) => {
    const now = Math.floor(Date.now() / 1000)
    let status = "Created"
    let variant = "outline"

    if (now < startTime) {
      status = "Upcoming"
      variant = "outline"
    } else if (now < endTime) {
      status = "Active"
      variant = "default"
    } else {
      status = "Ended"
      variant = "destructive"
    }

    return <Badge variant={variant}>{status}</Badge>
  }

  if (loading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {[1, 2, 3].map((i) => (
          <Card key={i}>
            <CardHeader>
              <Skeleton className="h-6 w-3/4 mb-2" />
              <Skeleton className="h-4 w-1/2" />
            </CardHeader>
            <CardContent>
              <Skeleton className="h-20 w-full" />
            </CardContent>
            <CardFooter>
              <Skeleton className="h-10 w-full" />
            </CardFooter>
          </Card>
        ))}
      </div>
    )
  }

  if (campaigns.length === 0) {
    return (
      <div className="text-center py-12">
        <h3 className="text-xl font-medium mb-4">No campaigns found</h3>
        <p className="text-muted-foreground mb-6">Be the first to create a voting campaign!</p>
        <Button asChild>
          <Link href="/create">Create Campaign</Link>
        </Button>
      </div>
    )
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {campaigns.map((campaign) => (
        <Card key={campaign.id} className="flex flex-col">
          <CardHeader>
            <div className="flex justify-between items-start">
              <CardTitle className="line-clamp-1">{campaign.description}</CardTitle>
              {getStatusBadge(campaign.startTime, campaign.endTime)}
            </div>
            <CardDescription>
              Created by: {campaign.creator.substring(0, 6)}...{campaign.creator.substring(38)}
            </CardDescription>
          </CardHeader>
          <CardContent className="flex-grow">
            <div className="text-sm text-muted-foreground">
              <div>Start: {formatDate(campaign.startTime * 1000)}</div>
              <div>End: {formatDate(campaign.endTime * 1000)}</div>
              <div className="mt-2">
                <Badge variant="outline" className="mr-2">
                  {campaign.votingType === 0 ? "Candidate-based" : "Proposal-based"}
                </Badge>
                <Badge variant="outline">
                  {campaign.restriction === 0 ? "Open" : campaign.restriction === 1 ? "Limited" : "Registration"}
                </Badge>
              </div>
            </div>
          </CardContent>
          <CardFooter>
            <Button asChild className="w-full">
              <Link href={`/campaigns/${campaign.id}`}>View Campaign</Link>
            </Button>
          </CardFooter>
        </Card>
      ))}
    </div>
  )
}
